// app/catalog/plate/page.tsx
"use client"
import * as React from "react"
import { Input } from "@/components/ui/input"
export default function Page() {
  const [plate,setPlate]=React.useState("")
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const [data,setData]=React.useState<any>(null)
  async function run(){
    const res=await fetch(`/api/plates?plate=${encodeURIComponent(plate)}&country=nl`)
    setData(await res.json())
  }
  return (
    <div className="p-6 space-y-3">
      <div className="flex gap-2">
        <Input placeholder="Kenteken (bv. G123AB)" value={plate} onChange={e=>setPlate(e.target.value)} onKeyDown={e=>e.key==="Enter"&&run()} />
        <button className="px-3 py-2 border rounded-xl" onClick={run}>Zoek</button>
      </div>
      <pre className="text-xs overflow-auto bg-muted/30 p-3 rounded-xl">{JSON.stringify(data,null,2)}</pre>
    </div>
  )
}
